import { ChangeDetectionStrategy, Component, Input } from '@angular/core';

import { LoanInfoItemType } from '../loan-info.model';
import { Loan } from '../../models/loan.model';

@Component({
  selector: 'bb-loan-info-item',
  templateUrl: './loan-info-item.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoanInfoItemComponent<T extends Loan = Loan> {
  /**
   * Item title
   */
  @Input() title!: string;
  /**
   * Item value
   */
  @Input() value!: T[keyof T];
  /**
   * Loan item type
   */
  @Input() type!: LoanInfoItemType;
  /**
   * Currency code
   */
  @Input() currencyCode: string | undefined;

  /**
   * Whether currency local should be transformed to symbol
   */
  @Input() isCurrencySymbolUsed!: boolean;

  /**
   * Item types config
   */
  readonly itemTypes = LoanInfoItemType;
}
